html {
  height: 100vh;
}

.no-select {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently */
}

.App {
  text-align: center;
  font-size: 62.5%;
  background: linear-gradient(to top left, rgb(1, 53, 88), rgb(206, 237, 241));
  height: 100vh;
}

.title {
  font-size: 7.6rem;
  background: linear-gradient(rgb(201, 37, 138), rgb(37, 42, 121));
  background: -webkit-linear-gradient(rgb(201, 37, 138), rgb(37, 42, 121));
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: "Dancing Script", cursive;
  font-family: "Monoton", cursive;
  color: #8695c7;
}

.attempt-line {
  margin: auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 50rem;
  transition: all 2s ease;
}

.letter-square {
  height: 7rem;
  width: 7rem;
  border: 2.2px solid rgb(50, 102, 161);
  margin: 1rem;
  border-radius: 0.7rem;
  background: linear-gradient(to top right, #ccc, #eee);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 4.2rem;
  color: rgb(61, 65, 124);
  transition: all 2s ease;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.2);
}

.input {
  margin: 0;
  width: 100%;
  position: fixed;
  bottom: 5%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.kb-row {
  display: flex;
  align-items: center;
  flex-direction: row;
  height: 6rem;
}

.key {
  box-sizing: border-box;
  padding: 1rem;
  height: 4rem;
  min-width: 4rem;
  font-size: 1.8rem;
  margin: 0 0.2rem 0 0.2rem;
  background: linear-gradient(to top right, #ccc, #eee);
  border-radius: 0.3rem;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border: none;
}

.key:hover {
  background: linear-gradient(
    to top right,
    rgb(209, 209, 195),
    rgb(223, 223, 202)
  );
}

*:disabled {
  cursor: default;
}

.key-text {
  pointer-events: none;
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently */
}

.answer-submit-btn {
  height: 4.5rem;
  width: 12rem;
  border-radius: 0.5rem;
  border: none;
  margin-top: 1.5rem;
  font-size: 1.5rem;
  background: linear-gradient(to top left, rgb(83, 9, 67), rgb(22, 10, 88));
  color: white;
  cursor: pointer;
}

.shrink {
  height: 4rem;
}

.shrink > div {
  height: 3rem;
  width: 3rem;
  font-size: 2rem;
  margin: 0.4rem;
}

.correct {
  color: white;
  background: linear-gradient(to bottom right, rgb(23, 85, 11), rgb(14, 80, 5));
}

.present {
  color: white;
  background: linear-gradient(
    to bottom right,
    rgb(194, 166, 10),
    rgb(163, 127, 7)
  );
}

.complete-modal {
  position: fixed;
  box-sizing: border-box;
  left: 50%;
  top: 50%;
  padding: 2rem;
  transform: translate(-50%, -50%);
  height: 14rem;
  width: 60rem;
  color: white;
  font-size: 2rem;
  background: linear-gradient(to top right, rgb(27, 45, 97), rgb(2, 97, 104));
  border-radius: 1rem;
  z-index: 100;
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.2);
}

.complete-btn {
  height: 4rem;
  width: 16rem;
  font-size: 1.8rem;
  border-radius: 0.5rem;
  background: linear-gradient(to top right, rgb(158, 118, 9), rgb(207, 188, 9));
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.3);
  outline: none;
  border: none;
  cursor: pointer;
}

*:disabled {
  background: linear-gradient(to top right, #333, #555);
  opacity: 0.5;
  color: white;
}

*:disabled:hover {
  background: linear-gradient(to top right, #333, #555);
}
